import React, { Component } from 'react';
export default class Resume extends Component {
	  render() {
		let resumeData = this.props.resumeData;
		return (
			<React.Fragment>
			<section id="resume">
			<div className="row education">
			  <div className="three columns header-col">
				<h1><span>Education</span></h1>
			  </div>
			  <div className="nine columns main-col">
				{resumeData.education.map( item => (
					<div className="row item">
					  <div className="twelve columns">
						<h3>{item.place}</h3>
						<p className="info">{item.role} <span>•</span> <em className="date">{item.dates}</em></p>
						<p>{item.description}</p>
					  </div>
					</div>
				))}
			  </div> {/* main-col end */}
			</div> {/* End Education */}

			<div className="row work">
			  <div className="three columns header-col">
				<h1><span>Work</span></h1>
			  </div>
			  <div className="nine columns main-col">
				{resumeData.work.map( item => (
					<div className="row item">
					  <div className="twelve columns">
						<h3>{item.place}</h3>
						<p className="info">{item.role} <span>•</span> <em className="date">{item.dates}</em></p>
						<p>{item.description}</p>
					  </div>
					</div>
				))}
			  </div> {/* main-col end */}
			</div> {/* End Work */}

			<div className="row skill">
			  <div className="three columns header-col">
				<h1><span>Skills</span></h1>
			  </div>
			  <div className="nine columns main-col">
				<p>I have a broad range of design and front end programming skills including but not limited to:
				</p>
				<div className="bars">
				  <ul className="skills">
					<li><span className="bar-expand photoshop" /><em>Photoshop</em></li>
					<li><span className="bar-expand illustrator" /><em>Illustrator</em></li>
					<li><span className="bar-expand wordpress" /><em>Wordpress</em></li>
					<li><span className="bar-expand squarespace" /><em>Squarespace</em></li>
					<li><span className="bar-expand css" /><em>CSS / LESS / SASS</em></li>
					<li><span className="bar-expand html5" /><em>HTML5</em></li>
					<li><span className="bar-expand jquery" /><em>jQuery</em></li>
					<li><span className="bar-expand reactjs" /><em>ReactJS</em></li>
					<li><span className="bar-expand d3js" /><em>D3.js</em></li>
				  </ul>
				</div>{/* end skill-bars */}
			  </div> {/* main-col end */}
			</div> {/* End skills */}
		  </section> {/* Resume Section End*/}
		  </React.Fragment>
		);
	  }
}